@import '~normalize.css';
@import 'variables.scss';

@import 'pages/homepage.scss';
@import 'pages/shop.scss';

@import 'sections/navigation.scss';
@import 'sections/cart.scss';
@import 'sections/productModal.scss';

* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  color: #000;
}

.container {
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
  &.small {
    max-width: 930px;
  }
}

.buy__btn {
  background-color: $regularLinkColor;
  border: 0;
  border-radius: 20px;
  color: #fff;
  padding: 10px 15px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color .2s ease-in-out;
  &:hover {
    background-color: darken($regularLinkColor, 5%);
  }
}

.error {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 200px);
  &__text {
    text-align: center;
    font-size: 3.5rem;
    font-weight: 500;
    margin: 0;
    max-width: 750px;
  }
}